import React from 'react';
import Image from 'next/image';

function Page() {
  const styles = {
    daleman: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      minHeight: "100vh",
      backgroundColor: "#F3E5D8",
      fontFamily: "'Poppins', sans-serif",
      padding: "40px",
    },
    grid: {
      display: "grid",
      gridTemplateColumns: "repeat(3, 1fr)",
      gap: "30px",
    },
    card: {
      border: "3px solid #A67B5B",
      borderRadius: "16px",
      overflow: "hidden",
      boxShadow: "0 4px 15px rgba(0,0,0,0.1)",
      width: "250px",
      height: "250px",
      position: "relative",
      backgroundColor: "#fff",
    },
    img: {
      width: "100%",
      height: "100%",
      objectFit: "cover",
    },
    text: {
      position: "absolute",
      top: "10px",
      left: "50%",
      transform: "translateX(-50%)",
      backgroundColor: "rgba(0,0,0,0.6)",
      color: "white",
      padding: "4px 10px",
      borderRadius: "8px",
      fontSize: "14px",
      fontWeight: "bold",
    },
  };

  return (
    <div style={styles.daleman}>
      <div style={styles.grid}>
        <div style={styles.card}>
          <span style={styles.text}>Tumbler</span>
          <img src="/assest/tumbler.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Boyfie</span>
          <img src="/assest/boyfie.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Buku</span>
          <img src="/assest/bukuu.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>No No</span>
          <img src="/assest/nono.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Hi</span>
          <img src="/assest/hi.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Stress lv.10</span>
          <img src="/assest/lvl10.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Stress lagi</span>
          <img src="/assest/lvl10.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Boyfie lagi</span>
          <img src="/assest/boyfie.jpg" alt="tumbler" style={styles.img} />
        </div>
        <div style={styles.card}>
          <span style={styles.text}>Hi lagi</span>
          <img src="/assest/hi.jpg" alt="tumbler" style={styles.img} />
        </div>
      </div>
    </div>
  );
}

export default Page;
